.track{
    height:19.96vh;
    width:95vw;
    margin-bottom:1vh;
    margin-left:3vw;
}
.album-art{
    height: 19.96vh;
    width:19.96vh;
    float: left;
}
.track .descriptor {
    padding-top:0.003vh;
    color:white;
    text-align: center;
    height:20vh;
    width:auto;
    background-color: rgb(26, 26, 26);
    margin-top: 2vh;

}

.track-play-button{
    width:10vw;
    font-size:25px;
    background-color: transparent;
    border:none;

}
.track-play-button:focus{
    background-color: transparent;
}
@media (max-height: 780px){
    .track{
        display:flex;
        flex-direction: row;
    }
}

