.prog-bar-container{
    display: flex;
    flex-direction: row;
    margin-top: 0vw;
    justify-content: center;
}
.time-div{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 400;
}

.prog-bar-container .progress-bar {
    margin: 0 5px 0 5px;
    width: 45vw;
    height: 23px;
    border: 10px;
    border-radius: 45px;
    background: rgb(15, 15, 15);
}
@media (max-width: 480px){
    .prog-bar-container{
        margin-top: 1px;
        margin-bottom: 10px;
    }
    .prog-bar-container .progress-bar {
        width: 70vw;
    }
}