.cover-art{
    box-shadow: 2px 5px 2px rgba(8, 8, 8, 0.767);
    border-radius: 5px;
}
.empty-cover{
    background: #121212;
    margin:0 auto;
    height: 300px;
    width: 300px;
}
.player-interface{
    display: flex;
    background: linear-gradient(#0c0c0c, #212121);
    flex-direction: row;
    width: 100vw;
    padding: 70px 0px 10px 0px;
    margin: 0;
}

.control-pane{
    flex-grow: 2;
    border-radius:5px;
    background-color: rgba(22, 22, 22, 0.712);
    box-shadow: 2px 5px 2px rgba(8, 8, 8, 0.767);
}
.details-pane{
    margin-top:45px;
    height:100px;
    padding-right:0px;
    color: white;
}
.control-panel{
    margin-top: 3em;
    text-align: center;
    display:flex;
    flex-direction: column;
}
.control-cluster{
    height: auto;
    display:flex;
    justify-content: center;
    flex-direction: row;

}
.artists-list{
    margin-top: 2.5vh;
    font-weight:500;
    text-align: center;
}
@media (max-width: 480px){
    .player-interface{
        display: flex;
        flex-direction: column;
    }
    .cover-art{
        margin: 2vh 10vw 2vh 10vw;
    }
    .control-pane{
        margin: 0 15px 0 8px;
    }
    .details-pane{
       margin-top: 0px;
       margin-bottom: 1vh;
       font-size: 12px;
    }
    .control-panel{
        margin: 0 0 15px 0;
    }
    .artists-list{
        margin-top: -1vh;
    }
}